export default {
	data() {
		return {
			//判断小程序的API，回调，参数，组件等是否在当前版本可用。
			canIUse: wx.canIUse('button.open-type.getUserInfo'),
		}
	},
	created() {
		this.wxLogin();
	},
	methods: {
		wxLogin() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					if (!res.code) {
						return;
					}
					this.$ajax('/wxLogin', {
						code: res.code
					}).then(data => {
						if(data.status) {
							uni.setStorage({
								key: 'token',
								data: data.token
							});
						}
					});
				},
			});
		},
		bindGetUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: infoRes => {
					const token = uni.getStorageSync('token');
					this.$ajax('/wxUserInfo', {
						userInfo: infoRes,
						token: token
					}).then(data => {
						if (data.status) {
							this.cacheUserInfo(infoRes);
							//跳转到主页
							uni.switchTab({
								url: '/pages/main/main'
							});
						} else {
							uni.showToast({
								title: '登录失败',
								icon: 'none',
								duration: 2000
							});
						}
					});
				}
			});
		},
		cacheUserInfo(userInfo) {
			uni.setStorage({
				key: 'user_info',
				data: userInfo.rawData,
				success: function() {
					console.log('用户信息已缓存');
				}
			});
		}
	},
	onShow() {}
}
